@font-face {
  font-family: FiraCode;
  src: url("/fonts/FiraCode-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}

/* Media Queries */
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
}

body {
  color: #212121;
  background-color: #fafafa;
  font-family: "Open Sans", sans-serif;
  line-height: 2em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #212121;
  font-family: "Abel", sans-serif;
}

code {
  background-color: #000;
  border: 1px solid #404040;
  border-radius: 4px;
  color: #fff;
  font-family: "FiraCode";
  font-weight: lighter;
  padding: 10px 20px;
  margin: 10px 0 0;
}

code::before {
  color: #00cc00;
  content: '$ ';
}

li,
ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.site-container {
  margin: auto;
  max-width: 1170px;
  width: 98%;
  margin-bottom: 50px;
}

.homework {
  display: grid;
  margin: auto;
  width: 90%;
}

.homework p {
  margin-top: 0;
}

@media only screen and (min-width: 768px) {
  .homework {
    grid-template-columns: 1fr 1fr;
  }
  .homework h3 {
    grid-column: 1 / -1;
  }
}

@media only screen and (min-width: 768px) {
  .header-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.gray-container {
  background-color: #f2f2f2;
  border: 1px solid #cccccc;
  border-radius: 4px;
  margin-top: 20px;
  width: 100%;
}

.large-container {
  padding-top: 40px;
  padding-bottom: 50px;
}

.title-container {
  background-color: #F6F8FA;
  border-bottom: 1px solid #DDDDDD;
  padding: 5px 10px;
}

.title-container h2 {
  padding: 0;
  margin: 0;
}

.homework-container {
  border: 1px solid #DDDDDD;
  border-radius: 4px;
  margin-top: 20px;
}

.main-heading {
  margin: auto auto 50px;
  width: 90%;
}

@media only screen and (min-width: 768px) {
  .main-heading {
    margin: auto;
    width: 80%;
  }
}

.skills {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: grid;
  grid-gap: 10px;
  grid-template-areas: 'html css js' '. gulp .' 'sass npm node';
  margin: auto;
  width: 90%;
}

.skills #html {
  grid-area: html;
}

.skills #css {
  grid-area: css;
}

.skills #js {
  grid-area: js;
}

.skills #sass {
  grid-area: sass;
}

.skills #gulp {
  grid-area: gulp;
}

.skills #npm {
  grid-area: npm;
}

.skills #node {
  grid-area: node;
}

@media only screen and (min-width: 768px) {
  .skills {
    justify-items: center;
    margin: 0;
  }
}
